.team-prog {

    background: linear-gradient(217deg, rgba(203, 210, 218, 0.747), rgba(60, 99, 228, 0) 70.71%), 
                linear-gradient(127deg, rgba(25, 83, 150, 0.8), rgb(33, 157, 105) 70.71%), 
                linear-gradient(336deg, rgba(243, 80, 4, 0.8), rgba(25, 83, 150, 0.8) 70.71%); 
    margin-top: -20px;
                
    &__title {
        text-align: left;
        margin-left: 5%;
        padding-top: 50px;
    }

    @media screen and (max-width: 767px) { 
        &__container {        
            display: flex;
            flex-direction: column;
        }}   

    @media screen and (min-width: 768px) { 
        &__container {        
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }}    
        
    @media screen and (max-width: 767px) { 
    &__intro {
        display: block;
        margin-right: 5px;
        margin-left: 10%;
        width: 80%;
    }}

    @media screen and (min-width: 768px) { 
    &__intro {
        display: block;
        margin-left: 10%;
        margin-right: 10%;
        width: 50%;
        margin-top: 2%;
    }}

    @media screen and (min-width: 1440px) { 
    &__intro {
        font-size: 1.5rem;
    }}
    
    @media screen and (max-width: 767px) { 
    &__slide {
        display: block;
        margin-left: 10%;
        width:  75%;
        margin-right: 10%;
        margin-bottom: 5%;
    }}

    @media screen and (min-width: 768px) { 
    &__slide {
        display: block;
        margin-left: 10%;
        width:  75%;
        margin-right: 10%;
        margin-bottom: 5%;
    }}
}